body {
  padding: 0px;
}

ul::marker {
  display: none;
}

#nav-item-1 {
  margin-right: 20px;
}
.navbar {
  height: 80px;
  display: flex;
  align-items: center;
  /* position: sticky; */
  top: 0;
  z-index: 1000;
  font-size: 1.1rem;
  /* box-shadow: 0px 20px 18px -20px rgba(0, 0, 0, 0.42); */
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90px;
  width: 100vw;
}

.icon-cont-cont {
  margin-left: 85px;
  left: 0;
}

.icon-container {
  margin-left: 50px;
  justify-self: flex-start;
  cursor: pointer;

  display: flex;
  align-items: center;

  padding: 1rem 1rem 0rem 1rem;
  height: 100%;
  width: 300px;
}

#icon {
  height: 40px;
  width: auto;
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(1, auto);
  grid-gap: 15px;
  list-style: none;
  margin-right: 160px;
  margin-left: 160px;
  width: 100vw;
  text-align: center;
}

.nav-item {
  height: 70px;
}

.nav-links {
  /* color: rgb(53, 49, 49); */
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 1rem 1rem 1rem 1rem;
  margin-right: 0.8rem;
  padding-bottom: 0rem;
  height: 100%;
  /* font-family: "Cormorant", serif; */
  /* font-weight: 300; */
}
.nav-links-last {
  /* color: rgb(53, 49, 49); */
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 1rem 1rem 1rem 1rem;
  padding-bottom: 0rem;
  height: 100%;
  /* font-family: "Cormorant", serif; */
  /* font-weight: 300; */
}

.nav-links:hover,
.nav-links-last:hover {
  text-decoration: underline;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #292929;
}
.fa-times {
  font-size: 1.8rem;
  color: #484848;
  z-index: 100;
  display: absolute;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  body {
    padding: 0px;
    max-width: 100vw;
  }

  .navbar {
    height: 80px;
  }

  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;

    width: 100vw;
    height: 90vh;
    position: absolute;

    opacity: 1;
    left: -150vw;
    top: 80px;
    transition: all 0.5s ease;

    justify-content: flex-start;
  }

  .nav-menu.active {
    margin-top: 80px;
    top: 0px;
    left: 0;
    width: 100vw;
    background: #222222;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    margin-left: 0;
    color: white;
  }
  .mobile-nav {
    flex-direction: column;
  }
  .nav-links,
  .nav-links-last {
    display: flex;
    flex-direction: column;

    text-align: center;
    justify-content: center;
    margin: 2rem auto;

    padding: 5px 10px;
    border-radius: 4px;
    width: 70%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #ffffff;
  }

  .nav-item {
    height: 90px;
  }

  .nav-links:hover {
    text-decoration: underline;
    background-color: rgb(47, 47, 47);
    transition: all 0.2s ease-out;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    font-size: 1.8rem;
    color: #484848;
    z-index: 100;
    display: absolute;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #ffffff;
    padding: 14px 20px;
    border: 1px solid #fff;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: rgb(53, 49, 49);
  }

  .shopping-icon {
    display: block;
    position: relative;
    transform: translate(-100%, 10%);
    font-size: 1.8rem;
    cursor: pointer;
    font-weight: 100;
    justify-content: end;
    justify-self: flex-end;
  }
  .icon-container {
    margin-left: 5px;
  }

  .icon-cont-cont {
    margin-left: 0px;
    left: 0;
    transform: translate(0%, -8%);
    display: block;
    position: absolute;
  }
  /* .icon-cont-cont {
      margin-left: 0px;
      padding: 1rem 1rem 0rem 0rem;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(0%, -8%);
    } */
}
